import React from "react";
import { Link } from "react-router-dom";
import { NavLink } from "react-router-dom";
import { connect } from "react-redux";
import { Navbar, Nav, NavDropdown } from "react-bootstrap";

import SignedInLinks from "../auth/SignedInLinks";
import SignedOutLinks from "../auth/SignedOutLinks";
import Logo from "../../assets/images/loguna.png";
import { signOut } from "../../store/actions/authActions";

class NavigationBar extends React.Component {
  render() {
    const { auth } = this.props;
    const links = auth.uid ? <SignedInLinks /> : <SignedOutLinks />;
    return (
      <Navbar expand="md" className="navigation-bar sticky-top">
        <Link to="/">
          <Navbar.Brand>
            <img className="Logo" src={Logo} alt="logo" />
          </Navbar.Brand>
        </Link>

        <Navbar.Toggle
          className="toggle-button ml-auto"
          aria-expanded="false"
          aria-controls="basic-navbar-nav"
        />
        <div className="collapse-container">
          <Navbar.Collapse
            id="basic-navbar-nav"
            className="justify-content-around"
          >
            <NavLink to="/" className="nav-link">
              Home
            </NavLink>
            <NavLink to="/signin" className="nav-link">
              What We Offer
            </NavLink>

            <NavDropdown title="Services" id="nav-dropdown" className="services-dropdown">
              <div>d</div>
            </NavDropdown>

            <NavDropdown title="Contact" id="nav-dropdown" className="">
              <NavDropdown.Item eventKey="4.1">Contact Us</NavDropdown.Item>
              <NavDropdown.Item eventKey="4.2">Get a Quote</NavDropdown.Item>
              <NavDropdown.Item eventKey="4.3">Something else</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item eventKey="4.4">Separated Link</NavDropdown.Item>
            </NavDropdown>
            <div className="auth-collapse">
              {auth.uid ? (
                <div>
                  <NavLink to="/signin" className="nav-link toggle-signin">
                    My Account
                  </NavLink>
                  <Nav.Link className="nav-link" onClick={this.props.signOut}>
                    Log Out
                  </Nav.Link>
                </div>
              ) : (
                <div>
                  <NavLink to="/signin" className="nav-link toggle-signin">
                    Sign In
                  </NavLink>
                  <NavLink to="/signup" className="nav-link  toggle-signup">
                    Sign Up
                  </NavLink>
                </div>
              )}
            </div>
          </Navbar.Collapse>
        </div>

        <Nav className="auth"> {links} </Nav>
      </Navbar>
    );
  }
}

const mapStateToProps = state => {
  // console.log(state);
  return {
    auth: state.firebase.auth
  };
};

const mapDispatchToProps = dispatch => {
  return {
    signOut: () => dispatch(signOut())
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(NavigationBar);
